let CoreClass

if (PSPDFKIT_DEFINES.STANDALONE_NATIVE) {
  CoreClass = require('../../core/CoreNative').default
} else if (PSPDFKIT_DEFINES.WASM_IN_MAIN_THREAD) {
  CoreClass = require('../../core/Core').default
} else {
  CoreClass = require('../../core/CoreClient').default
}

export const corePool: ObjectPool<Core, any> = new ObjectPool(CoreClass)

export default class StandaloneBackend extends SharedBackend {
  provider: StandaloneProvider
  client: Core
  checkIn: () => void
  type: BackendType = 'STANDALONE'
  _state: StandaloneBackendState
  _requestQueue: RequestQueue
  _instantJSON: InstantJSON | null | undefined
  _isPDFJavaScriptEnabled: boolean | null | undefined

  /**
   * If configured by our customers, we call this callback to get the list of custom root
   * certificates to use for digital signatures validation.
   */
  _trustedCAsCallback: TrustedCAsCallback | null | undefined
  _XFDF:
    | {
        source: string
        keepCurrentAnnotations: boolean
      }
    | null
    | undefined = null

  constructor(configuration: StandaloneConfiguration) {
    super()
    validateStandaloneConfiguration(configuration)

    const {
      baseUrl,
      instantJSON,
      XFDF,
      enableAutomaticLinkExtraction,
      overrideMemoryLimit,
      trustedCAsCallback,
      electronAppName,
      useSplitWASMModule,
    } = configuration

    if (typeof XFDF === 'string') {
      this._XFDF = {
        source: XFDF,
        keepCurrentAnnotations: configuration.XFDFKeepCurrentAnnotations === true,
      }
    }

    // Validate instantJSON
    if (instantJSON && instantJSON.annotations) {
      instantJSON.annotations = instantJSON.annotations.map(annotation => {
        annotation.id = annotation.id.toString()

        return annotation
      })
    }

    this._instantJSON = instantJSON

    if (typeof trustedCAsCallback === 'function') {
      this._trustedCAsCallback = trustedCAsCallback
    }

    // Validate disableWebAssembly
    const { disableWebAssembly, disableWebAssemblyStreaming, customFonts } = configuration
    // Validate standaloneInstancesPoolSize
    const { standaloneInstancesPoolSize } = configuration

    if (typeof standaloneInstancesPoolSize !== 'undefined') {
      corePool.size = standaloneInstancesPoolSize
    }

    const forceLegacySignaturesFeature = configuration.electronicSignatures
      ? Boolean(configuration.electronicSignatures.forceLegacySignaturesFeature)
      : false

    this._state = new StandaloneBackendState(
      normalizeCoreOptions({
        baseUrl: baseUrl as string,
        licenseKey: configuration.licenseKey,
        document: configuration.document,
        disableWebAssembly,
        disableWebAssemblyStreaming,
        enableAutomaticLinkExtraction,
        overrideMemoryLimit,
        documentHandle: '0',
        customFonts,
        forceLegacySignaturesFeature,
        electronAppName,
        useSplitWASMModule,
      }),
    )
    this._requestQueue = new RequestQueue(MAX_RESOURCE_REQUESTS_IN_QUEUE_STANDALONE)

    const { object, checkIn } = corePool.checkOut()

    this.client = object
    this.checkIn = checkIn

    const providerJSON = instantJSON
      ? {
          annotations: instantJSON.annotations || [],
          formFields: instantJSON.formFields || [],
          formFieldValues: instantJSON.formFieldValues || [],
          skippedPdfObjectIds: instantJSON.skippedPdfObjectIds || [],
          skippedPdfFormFieldIds: instantJSON.skippedPdfFormFieldIds || [],
          attachments: instantJSON.attachments || {},
          bookmarks: instantJSON.bookmarks || [],
          skippedPdfBookmarkIds: instantJSON.skippedPdfBookmarkIds || [],
          format: instantJSON.format,
          ...(instantJSON.pdfId ? { pdfId: instantJSON.pdfId } : null),
        }
      : null

    this.provider = new StandaloneProvider(this.client, providerJSON)
  }

  isUsingInstantProvider(): boolean {
    return false
  }

  hasClientsPresence(): boolean {
    return false
  }

  async load(options: LoadOptions = {}): Promise<LoadResponse> {
    this._isPDFJavaScriptEnabled = options.isPDFJavaScriptEnabled

    return {
      features: this._state.features,
      signatureFeatureAvailability: this._state.signatureFeatureAvailability,
      hasPassword: !!options.password,
      allowedTileScales: 'all',
    }
  }

  destroy(): void {
    this.provider && this.provider.destroy()
    this._requestQueue && this._requestQueue.destroy()
    this.checkIn()
  }

  async documentInfo(): Promise<DocumentResponse> {
    return this._state.documentResponse
  }

  async lazyLoadPages(): Promise<Array<PageInfoResponse>> {
    if (!this._state.lazyLoadedPages) {
      const pageInfos = await this.client.getAllPageInfos(this._state.documentResponse.pageCount)

      this._state = this._state.set('lazyLoadedPages', pageInfos)
    }

    // this._state.lazyLoadedPages cannot be null now
    return this._state.lazyLoadedPages as Array<PageInfoResponse>
  }

  getDocumentHandle(): string {
    return this._state.documentHandle
  }

  getFormJSON(): Promise<FormResponse> {
    return this.client.getFormJSON()
  }

  permissions(): Promise<BackendPermissions> {
    return Promise.resolve(this._state.backendPermissions)
  }

  textForPageIndex(pageIndex: number): {
    promise: Promise<TextResponse>
    cancel: IFunction
  } {
    const identifier = `${pageIndex}-text`
    let isCanceled = false
    const request = new CallbackRequest(identifier, () => {
      if (isCanceled) {
        return Promise.reject({
          aborted: true,
        })
      }

      return this.client.textForPageIndex(pageIndex).then(json => {
        if (isCanceled) {
          // When the request has been cancelled we return a rejected Promise like aborted XHR do.
          return Promise.reject({
            aborted: true,
          })
        }

        return jsonToTextLines(
          {
            textLines: json,
          },
          pageIndex,
        )
      })
    })

    const { promise, cancel } = this._requestQueue.enqueue(request)

    return {
      promise,
      cancel: () => {
        isCanceled = true
        cancel()
      },
    }
  }

  getTextFromRects(pageIndex: number, rects: List<Rect>): Promise<string> {
    return this.client.getTextFromRects(pageIndex, rects.toJS())
  }

  renderTile(
    pageIndex: number,
    pageSize: Size,
    tileRect: Rect,
    renderForPrinting: boolean,
    contentState?: {
      annotations: List<Annotation>
      formFieldValues: List<FormFieldValue>
      attachments: Map<string, Attachment>
      formFields: List<FormField>
    } | null,
  ): {
    promise: Promise<ImageHandle>
    cancel: IFunction
  } {
    const identifier = `${pageIndex}-${pageSize.width}-${pageSize.height}-${tileRect.top}-${
      tileRect.left
    }-${tileRect.width}-${tileRect.height}-${this.getDocumentHandle()}`
    const request = new CallbackRequest(identifier, () => {
      const annotationsToRender = contentState
        ? contentState.annotations.filter(isModifiableAnnotation).map(serializeAnnotation).toJS()
        : null
      const formFieldValuesToRender = contentState
        ? contentState.formFieldValues.map(serializeFormFieldValue).toJS()
        : null

      return this.client
        .renderTile(
          pageIndex,
          pageSize.toObject(),
          // @ts-expect-error
          tileRect.toObject(),
          renderForPrinting,
          annotationsToRender || (this._state.forceAnnotationsRender ? [] : null),
          formFieldValuesToRender,
          returnImageFormatForImageDimensions({ width: tileRect.width, height: tileRect.height }),
        )
        .then(bufferOrUrl => {
          if (!bufferOrUrl) {
            warn(
              'The image buffer or URL is null, the tile cannot be rendered:',
              // @ts-expect-error
              `page: ${pageIndex}, page size: ${pageSize.toObject()}, tile rect: ${tileRect.toObject()}`,
            )

            return Promise.resolve(null)
          }

          if (typeof bufferOrUrl === 'string') {
            return imageUrlToImageHandle(bufferOrUrl as string)
          }

          return bufferToImageHandle({
            buffer: bufferOrUrl as ArrayBuffer,
            width: tileRect.width,
            height: tileRect.height,
          })
        })
    })
    const hasPriority = pageSize.width === tileRect.width && pageSize.height === tileRect.height

    const { promise, cancel } = this._requestQueue.enqueue(request, hasPriority)

    return {
      promise,
      cancel,
    }
  }

  renderAnnotation(
    annotation: Annotation,
    _formFieldValue: FormFieldValue | null | undefined,
    blob: Blob | null | undefined,
    width: number,
    height: number,
  ): {
    promise: Promise<ImageHandle | null | undefined>
    cancel: () => void
  } {
    const identifier = annotation.id
    const attachmentData = blob

    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const request = new CallbackRequest(identifier, () => {
      return this.client
        .renderAnnotation(
          serializeAnnotation(annotation),
          attachmentData as Blob,
          width,
          height,
          returnImageFormatForImageDimensions({ width, height }),
        )
        .then(buffer => {
          if (!buffer) {
            // @ts-expect-error
            warn('The image buffer is null, the annotation cannot be rendered', annotation.toJS())

            return Promise.resolve(null)
          }

          return bufferToImageHandle({
            buffer,
            width,
            height,
          })
        })
    })

    return this._requestQueue.enqueue(request, false)
  }

  async renderPageAnnotations(
    pageIndex: number,
    unfilteredAnnotations: List<Annotation>,
    scaleFactor: number,
  ): Promise<void> {
    const provider = this.provider

    invariant(
      provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const formFieldValues: Array<FormFieldValueJSON> = []

    const formFields: Array<FormField> = []

    const hasWidgets = unfilteredAnnotations.some(
      annotation => annotation instanceof WidgetAnnotation,
    )

    if (hasWidgets) {
      await provider._setReadStateCallbacksPromise
    }

    // Filter out stamps, image and signature widgets, which do not get cached.
    const annotations = unfilteredAnnotations.filter(annotation => {
      const annotationWithFormField = hasWidgets
        ? (provider._readStateCallbacks as ReadStateProviderCallbacks).getAnnotationWithFormField(
            annotation.id,
          )
        : null
      const formField = annotationWithFormField?.formField

      const isCacheable = isCacheableAnnotationAPStream(annotation, formField)

      if (isCacheable && formField) {
        const formFieldValueForFormField = formFieldValues.find(
          formFieldValue => formFieldValue.name === formField.name,
        )

        if (!formFieldValueForFormField) {
          formFieldValues.push(serializeFormFieldValue(formFieldValueFromFormField(formField)))
          formFields.push(formField)
        }
      }

      return isCacheable
    })

    // This helper is used inside this method at different moments after async operations
    // so as to discard AP streams of widgets of form fields which have modified their value
    // and which therefore need to be rendered again.
    function hasSameValueIfWidget(annotation, currentFormFields) {
      if (annotation?.formFieldName) {
        const formField = formFields.find(formField => formField.name === annotation.formFieldName)

        const currentFormField = currentFormFields.find(
          formField => formField.name === annotation.formFieldName,
        )

        if (!areFormFieldValuesEqual(formField, currentFormField)) {
          return false
        }
      }

      return true
    }

    const pageAPStreamsPromise = new Promise<void>((resolve, reject) => {
      // We do not pass the form field value in the case of widgets because we want to render the original
      // AP stream from the document.
      // UWP needs to know the type of the annotation as not all are supported and should be rendered.
      // UWP also doesn't support decimal numbers when rendering annotations
      const renderPageAnnotationsCall: Promise<Array<ArrayBuffer | null | undefined>> =
        !PSPDFKIT_DEFINES.STANDALONE_NATIVE
          ? this.client.renderPageAnnotations(
              pageIndex,
              annotations.map(annotation => annotation.pdfObjectId).toArray() as number[],
              annotations.map(annotation => annotation.boundingBox.width * scaleFactor).toArray(),
              annotations.map(annotation => annotation.boundingBox.height * scaleFactor).toArray(),
              returnImageFormat(),
            )
          : this.client.renderPageAnnotations(
              pageIndex,
              annotations.map(annotation => annotation.pdfObjectId).toArray(),
              annotations
                .map(annotation =>
                  parseInt((annotation.boundingBox.width * scaleFactor) as unknown as string),
                )
                .toArray(),
              annotations
                .map(annotation =>
                  parseInt((annotation.boundingBox.height * scaleFactor) as unknown as string),
                )
                .toArray(),
              returnImageFormat(),
              // Required for UWP
              // @ts-expect-error
              annotations.map(annotation => typeForAnnotation(annotation.constructor)).toArray(),
            )

      renderPageAnnotationsCall
        .then((pageAPStreamsBuffers: Array<ArrayBuffer | null | undefined>) => {
          // Get updated copy of the form fields to check for values updated in the interim.
          const currentFormFields = formFields
            .map(formField => provider._readStateCallbacks?.getFormFieldByName(formField.name))
            .filter(Boolean) as FormField[]

          // An array of buffers with PNG images is received.
          const imageHandlePromises: Array<Promise<ImageHandle | null | undefined>> =
            pageAPStreamsBuffers.map((buffer, index) => {
              const annotation: Annotation | null | undefined = annotations.get(index)

              // AP streams of widgets associated with a form field which has modified its value
              // are discarded.
              if (!hasSameValueIfWidget(annotation, currentFormFields)) {
                return Promise.resolve(null)
              }

              if (annotation) {
                if (!buffer) {
                  warn(
                    'The image buffer is null, the annotation cannot be rendered',
                    // @ts-expect-error
                    annotation.toJS(),
                  )

                  return Promise.resolve(null)
                }

                // Each buffer is transformed to an image element with the PNG image as src
                return bufferToImageHandle({
                  buffer,
                  width: PSPDFKIT_DEFINES.STANDALONE_NATIVE
                    ? parseInt((annotation.boundingBox.width * scaleFactor) as unknown as string)
                    : annotation.boundingBox.width * scaleFactor,
                  height: PSPDFKIT_DEFINES.STANDALONE_NATIVE
                    ? parseInt((annotation.boundingBox.height * scaleFactor) as unknown as string)
                    : annotation.boundingBox.height * scaleFactor,
                })
              }

              return Promise.resolve(null)
            })

          Promise.all(imageHandlePromises).then(imageHandles => {
            // Get updated copy of the form fields to check for values updated in the interim.
            const currentFormFields = formFields
              .map(formField => provider._readStateCallbacks?.getFormFieldByName(formField.name))
              .filter(Boolean) as FormField[]

            imageHandles.forEach((imageHandle, index) => {
              const annotation: Annotation | undefined = annotations.get(index)

              if (annotation) {
                const annotationAPStreamPromiseResolve = this.annotationAPStreamPromises.get(
                  annotation.id,
                )

                // When an AP stream is discarded because it belongs to a widget associated with a form field
                // which has modified its value, the AP stream render is resolved with null and the AP stream
                // is not cached.
                const valueIsKeptIfWidget = hasSameValueIfWidget(annotation, currentFormFields)

                // If there's a registered resolve callback, resolve with the imageHandle
                if (annotationAPStreamPromiseResolve) {
                  this.annotationAPStreamPromises = this.annotationAPStreamPromises.delete(
                    annotation.id,
                  )

                  annotationAPStreamPromiseResolve(valueIsKeptIfWidget ? imageHandle : null)
                }

                // Cache the imageHandle
                imageHandle && valueIsKeptIfWidget && this.cacheAPStream(imageHandle, annotation)
              }
            })
            resolve()
          })
        })
        .catch(reject)
    })

    // Store the promise so when cachedRenderAnnotation() is called, it can be awaited to ensure AP streams are available for rendering.
    this.pageAPStreamsPromises = this.pageAPStreamsPromises.set(pageIndex, pageAPStreamsPromise)

    return pageAPStreamsPromise
  }

  renderDetachedAnnotation(
    annotation: Annotation,
    blob: Blob | null | undefined,
    width: number,
    height: number,
  ): {
    promise: Promise<ImageHandle | null | undefined>
    cancel: IFunction
  } {
    // A detached annotation does not have an id, so we generate one
    // for the queue identifier.
    if (annotation.id) {
      throw new PSPDFKitError(`Detached annotations should not have an \`id\`: ${annotation.id}`)
    }

    const identifier = ulid()
    const attachmentData = blob
    const request = new CallbackRequest(identifier, () => {
      return this.client
        .renderDetachedAnnotation(
          serializeAnnotation(annotation),
          attachmentData,
          width,
          height,
          returnImageFormatForImageDimensions({ width, height }),
        )
        .then(buffer => {
          if (!buffer) {
            // @ts-expect-error
            warn('The image buffer is null, the annotation cannot be rendered', annotation.toJS())

            return Promise.resolve(null)
          }

          return bufferToImageHandle({
            buffer,
            width,
            height,
          })
        })
    })

    const { promise, cancel } = this._requestQueue.enqueue(request, false)

    return {
      promise,
      cancel,
    }
  }

  async getAttachment(attachmentId: string): Promise<Blob> {
    const [data, type] = await this.client.getAttachment(attachmentId)

    return new Blob([data], {
      type,
    })
  }

  async parseXFDF(xfdf: string): Promise<IParsedXFDF> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const { errors, formFieldValues, annotations } = await this.client.parseXFDF(xfdf)

    return {
      errors: errors?.map(error => ({
        errorMessage: error.error_message,
        type: error.type,
      })),
      formFieldValues: formFieldValues?.reduce((prev, curr) => {
        prev[curr.fqdn] = curr.values

        return prev
      }, {}),
      annotations: List(
        annotations?.map(annotation => deserializeAnnotation(generateAnnotationId(), annotation)) ||
          [],
      ),
    }
  }

  async search(
    searchTerm: string,
    startPageIndex: number,
    limitPages: number,
    caseSensitive: boolean,
    searchInAnnotations = false,
    searchType: ISearchType = SearchType.TEXT,
  ): Promise<List<SearchResult>> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const results = await this.client.search(
      searchTerm,
      startPageIndex,
      limitPages,
      caseSensitive,
      searchType,
    )

    return parseJsonSearchResults(
      results.filter(result => searchInAnnotations || !result.isAnnotation),
    )
  }

  async searchAndRedact(
    searchTerm: string | ISearchPattern,
    options: {
      searchType: ISearchType
      searchInAnnotations: boolean
      caseSensitive: boolean
      annotationPreset: RedactionAnnotationPreset
    },
    state: State,
  ): Promise<List<Annotation>> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const { totalPages } = state
    const results = await this.client.search(
      searchTerm,
      0,
      totalPages,
      options.caseSensitive,
      options.searchType,
    )

    return List(
      results
        .filter(result => options.searchInAnnotations || !result.isAnnotation)
        .map(result => {
          const rects = result.isAnnotation ? [result.annotationRect] : result.rectsOnPage

          const _rects = List(rects).map(jsonRect => {
            invariant(jsonRect)

            return JSONToRect(jsonRect)
          })

          return new RedactionAnnotation({
            ...newAnnotationProps(state),
            ...options.annotationPreset,
            pageIndex: result.pageIndex,
            rects: _rects,
            boundingBox: Rect.union(_rects),
          })
        }),
    )
  }

  exportPDF({
    flatten = false,
    incremental = false,
    saveForPrinting = false,
    format = 'pdf',
    excludeAnnotations = false,
    preserveInstantJSONChanges = true,
  }: ExportPDFFlags = {}): Promise<ArrayBuffer> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    return this.client
      .exportFile(
        flatten,
        incremental,
        saveForPrinting,
        format,
        excludeAnnotations,
        preserveInstantJSONChanges,
      )
      .then(([buffer, exportFileInfo]): ArrayBuffer => {
        // We are setting the mimeType and extension in the ArrayBuffer to preserve backwards compatibility for the exportPDF API.
        // In the future we might introduce type exportDocument = ExportPDFFlags => { doc: ArrayBuffer, documentInfo: { mimeType: string, extension: info }}
        // @ts-expect-error
        buffer.mimeType = exportFileInfo.mimeType
        // @ts-expect-error
        buffer.extension = exportFileInfo.extension

        return buffer
      })
  }

  exportXFDF(): Promise<string> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    return this.client.exportXFDF()
  }

  exportInstantJSON(): Promise<InstantJSON> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone build requires Standalone annotation provider.',
    )

    return this.client.exportInstantJSON()
  }

  getPDFURL({ includeComments = true, saveForPrinting, excludeAnnotations }: GetPDFURLFlags = {}): {
    promise: Promise<string>
    revoke: IFunction
  } {
    return this.generatePDFObjectURL({
      includeComments,
      saveForPrinting,
      excludeAnnotations,
    })
  }

  generatePDFObjectURL({
    includeComments = true,
    saveForPrinting,
    excludeAnnotations = false,
  }: GeneratePDFObjectURLFlags = {}): {
    promise: Promise<string>
    revoke: IFunction
  } {
    let objectUrl,
      isRevoked = false
    const promise = new Promise<string>(resolve => {
      this.exportPDF({
        flatten: true,
        includeComments,
        saveForPrinting,
        excludeAnnotations,
      }).then(arrayBuffer => {
        if (isRevoked) {
          return
        }

        const blob = new Blob([arrayBuffer], {
          // @ts-expect-error
          type: arrayBuffer.mimeType,
        })

        objectUrl = window.URL.createObjectURL(blob)
        resolve(objectUrl)
      })
    })

    return {
      promise,
      revoke: () => {
        if (objectUrl) {
          window.URL.revokeObjectURL(objectUrl)
        }

        isRevoked = true
      },
    }
  }

  async getDocumentOutline(): Promise<List<OutlineElement>> {
    const outlineJSON = await this.client.getDocumentOutline()

    return List(outlineJSON.map(documentOutlineFromJSON))
  }

  async onKeystrokeEvent(event: JavaScriptEventElement): Promise<Array<ActionChange>> {
    return await this.client.onKeystrokeEvent(event)
  }

  async evalFormValuesActions(newValues: List<FormFieldValue>): Promise<Array<ActionChange>> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    return this.client.evalFormValuesActions(newValues.map(serializeFormFieldValue).toJS())
  }

  async evalScript(
    source: string,
    formFieldName: string | null | undefined,
  ): Promise<Array<ActionChange>> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    return this.client.evalScript(source, formFieldName)
  }

  async setFormJSONUpdateBatchMode(enable: boolean): Promise<void> {
    return this.client.setFormJSONUpdateBatchMode(enable)
  }

  async applyOperationsAndReload(operations: Array<DocumentOperationJSON>): Promise<LoadResponse> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    try {
      let processedOperations, operationsDocuments

      if (PSPDFKIT_DEFINES.STANDALONE_NATIVE) {
        // We already have the ids/paths in the operations list, so we don't
        // need anything in operationsDocuments
        processedOperations = operations
        operationsDocuments = {}
      } else {
        ;({ processedOperations, operationsDocuments } = await getProcessedOperations(operations))
      }

      await this.client.applyOperations(processedOperations, operationsDocuments)
    } catch (e) {
      throw new PSPDFKitError(`Applying operations failed: ${e}`)
    }

    this.provider._state = this.provider._state.set('alreadyLoadedPages', Map())

    return this.reloadDocument()
  }

  async applyRedactionsAndReload(): Promise<LoadResponse> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    try {
      await this.client.applyRedactions()
    } catch (e) {
      throw new PSPDFKitError(`Applying redactions failed: ${e}`)
    }

    return this.reloadDocument()
  }

  async reloadDocument(): Promise<LoadResponse> {
    try {
      this.provider?.destroy()
      this.provider = new StandaloneProvider(this.client, null)
      this._state = this._state.set('lazyLoadedPages', null)

      const documentResponse = await this.client.reloadDocument()

      this._state = this._state
        .set('documentResponse', documentResponse)
        .set('documentHandle', (parseInt(this._state.documentHandle) + 1).toString())
        .set('signaturesInfoPromise', null)

      return {
        features: this._state.features,
        signatureFeatureAvailability: this._state.signatureFeatureAvailability,
        hasPassword: false,
        allowedTileScales: 'all',
      }
    } catch (e) {
      throw new PSPDFKitError(`Reloading failed: ${e}`)
    }
  }

  async getEmbeddedFiles(): Promise<List<EmbeddedFile>> {
    const embeddedFileLists = await this.client.getEmbeddedFilesList()

    return List(embeddedFileLists.map(({ id, ...content }) => fromJSON(id, content, true)))
  }

  async exportPDFWithOperations(operations: Array<DocumentOperationJSON>): Promise<ArrayBuffer> {
    try {
      invariant(
        this.provider instanceof StandaloneProvider,
        'Standalone can only use standalone annotation provider',
      )

      let processedOperations, operationsDocuments

      if (PSPDFKIT_DEFINES.STANDALONE_NATIVE) {
        // We already have the ids/paths in the operations list, so we don't
        // need anything in operationsDocuments
        processedOperations = operations
        operationsDocuments = {}
      } else {
        ;({ processedOperations, operationsDocuments } = await getProcessedOperations(operations))
      }

      return this.client.exportPDFWithOperations(processedOperations, operationsDocuments)
    } catch (e) {
      throw new PSPDFKitError(`Exporting PDF with operations failed: ${e}`)
    }
  }

  getSignaturesInfo() {
    try {
      invariant(
        this.provider instanceof StandaloneProvider,
        'Standalone can only use standalone annotation provider',
      )

      if (this._state.signaturesInfoPromise) {
        return this._state.signaturesInfoPromise
      }

      const signaturesInfoPromise = this.client
        .getSignaturesInfo()
        .then(signaturesInfoResponse => deserializeSignaturesInfo(signaturesInfoResponse))

      // We save the promise so that if `getSignaturesInfo()` is called again
      // before the promise resolves the new calls awaits for the promise as well!
      this._state = this._state.set('signaturesInfoPromise', signaturesInfoPromise)

      return signaturesInfoPromise
    } catch (e) {
      throw new PSPDFKitError(`Getting document signatures info: ${e}`)
    }
  }

  async refreshSignaturesInfo(): Promise<void> {
    this._state = this._state.set('signaturesInfoPromise', null)
  }

  async loadCertificates(certificates: Array<string>): Promise<void> {
    return this.client.loadCertificates(certificates)
  }

  /**
   * This method adds a digital signature to a document in 4 steps:
   *
   * 1. It prepares the document for signing by calling
   * `prepareSignInvisible()` with the optional estimated size
   * needed to include the signature in the document, which returns
   * a hash of the content prepared to be signed, and that content.
   * 2. it passes the returned hash and content to the callback provided
   * by the user, which returns a Promise that resolves with the PKCS7
   * container that will be embedded in the signed document.
   * 3. Passes the PKCS7 container to `sign()`, which embeds it
   * on the document.
   * 4. The document is reloaded, now digitally signed.
   */
  async signDocumentAndReload(
    signaturePreparationData: SignaturePreparationData | null | undefined,
    twoStepSignatureCallback: TwoStepSignatureCallback | (SigningServiceData | null | undefined),
  ): Promise<void> {
    invariant(
      typeof twoStepSignatureCallback === 'function',
      'Signing document failed: twoStepSignatureCallback must be a function',
    )

    const signerDataSource =
      signaturePreparationData && signaturePreparationData.placeholderSize
        ? {
            estimatedSize: signaturePreparationData.placeholderSize,
          }
        : null

    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    try {
      /**
       * 1. It prepares the document for signing by calling
       * `prepareSignInvisible()` with the optional estimated size
       * needed to include the signature in the document, which returns
       * the part of the file contents to be signed, and a hash of that
       * content.
       */
      const { hash, signatureFormFieldName, file, fileContents } =
        await this.client.prepareSignInvisible(
          signerDataSource,
          signaturePreparationData ? Boolean(signaturePreparationData.flatten) : false,
        )

      invariant(
        typeof twoStepSignatureCallback === 'function',
        'twoStepSignatureCallback must be a function',
      )

      /**
       * 2. it passes the returned hash and file contents to sign to the callback
       * provided by the user, which returns a Promise that resolves with the PKCS7
       * container that will be embedded in the signed document.
       */
      const result = await twoStepSignatureCallback({
        hash,
        fileContents,
      })
      let PKCS7Container: ArrayBuffer | null | undefined = undefined

      if (!(result instanceof ArrayBuffer)) {
        if (PSPDFKIT_DEFINES.STANDALONE_NATIVE) {
          // Convert the IBuffer to an ArrayBuffer
          const dataHeap = new Uint8ClampedArray(result)

          PKCS7Container = dataHeap.buffer
        } else {
          throw new PSPDFKitError(
            'Resolved value from twoStepSignatureCallback is of type ' +
              `${typeof result} and it must be an ArrayBuffer instead.`,
          )
        }
      } else {
        PKCS7Container = result
      }

      /**
       * 3. Passes the PKCS7 container to `sign()`, which embeds it
       * on the document.
       */
      await this.client.sign(file, signatureFormFieldName, arrayBufferToBase64(PKCS7Container))

      /**
       * 4. The document is reloaded, now digitally signed.
       */
      await this.reloadDocument()
    } catch (e) {
      // Restore the document backup in case digital signing fails.
      await this.client.restoreToOriginalState()
      throw e
    }
  }

  // Cancels all current requests and removes all associated promises.
  cancelRequests() {
    this._requestQueue.cancelAll()
  }

  async syncChanges(): Promise<void> {}

  getDefaultGroup() {
    return undefined
  }

  isCollaborationPermissionsEnabled() {
    // Collaboration Permissions is only available on ServerBackend
    return false
  }

  async clearAPStreamCache(): Promise<void> {
    return this.client.clearAPStreamCache()
  }

  // Document comparison methods
  async setComparisonDocument(
    documentKey: DocumentComparisonDocumentKey,
    buffer: ArrayBuffer | null,
  ): Promise<void> {
    return this.client.setComparisonDocument(documentKey, buffer)
  }

  async openComparisonDocument(
    documentKey: DocumentComparisonDocumentKey,
  ): Promise<DocumentResponse> {
    this._state = this._state.set('forceAnnotationsRender', false)
    await this.client.closeDocument()

    this._state = this._state.set('forceAnnotationsRender', true)

    return (await this.client.openComparisonDocument(documentKey)) || this._state.documentResponse
  }

  async documentCompareAndOpen(
    settings: DocumentComparisonConfigurationJSON,
  ): Promise<DocumentResponse> {
    return this.client.documentCompareAndOpen(settings)
  }

  async persistOpenDocument(
    copyDocumentKey: DocumentComparisonDocumentKey | undefined,
  ): Promise<void> {
    return this.client.persistOpenDocument(copyDocumentKey)
  }

  async cleanupDocumentComparison(): Promise<DocumentResponse> {
    return this.client.cleanupDocumentComparison()
  }

  async runPDFFormattingScripts(
    annotations: Map<string, Annotation>,
    isAPStreamRendered?: IsAPStreamRenderedCallback,
  ): Promise<ActionChange[]> {
    let jsActionChanges: ActionChange[] = []

    if (this._isPDFJavaScriptEnabled) {
      const initialChanges = await this.client.enablePDFJavaScriptSupport()

      const { withAPStream, withoutAPStream } = annotations.reduce(
        (acc: { withAPStream: string[]; withoutAPStream: string[] }, annotation) => {
          if (isAPStreamRendered?.(annotation)) {
            acc.withAPStream.push(annotation.formFieldName)
          } else {
            acc.withoutAPStream.push(annotation.formFieldName)
          }

          return acc
        },
        { withAPStream: [], withoutAPStream: [] },
      )

      let formattingChanges: ActionChange[] = []

      // Calling `runPDFFormattingScripts` with an empty array is treated as if the whole document
      // needs to be formatted, so we have to prevent sending empty array
      if (withAPStream.length && !withoutAPStream.length) {
        formattingChanges = await this.client.runPDFFormattingScripts(withAPStream, true)
      } else if (!withAPStream.length && withoutAPStream.length) {
        formattingChanges = await this.client.runPDFFormattingScripts(withoutAPStream, false)
      } else if (withAPStream.length && withoutAPStream.length) {
        const [withAPStreamResult, withoutAPStreamResult] = await Promise.all([
          this.client.runPDFFormattingScripts(withAPStream, true),
          this.client.runPDFFormattingScripts(withoutAPStream, false),
        ])

        formattingChanges = withAPStreamResult.concat(withoutAPStreamResult)
      }

      jsActionChanges = mergeActionChangesWithFormattingActionChanges(
        initialChanges,
        formattingChanges,
      )
    }

    return jsActionChanges
  }
}

/**
 * Normalize options passed to StandaloneBackendState, or even those passed
 * directly to CoreClient.
 */

export function normalizeCoreOptions(options: {
  baseUrl: string
  licenseKey?: null | string
  document: string | ArrayBuffer
  disableWebAssembly?: boolean
  disableWebAssemblyStreaming?: boolean
  enableAutomaticLinkExtraction?: boolean
  overrideMemoryLimit?: number
  documentHandle?: string
  trustedCAsCallback?: TrustedCAsCallback
  customFonts?: Array<Font>
  forceLegacySignaturesFeature?: boolean
  electronAppName?: string
  useSplitWASMModule?: boolean
}) {
  return {
    baseUrl: options.baseUrl,
    licenseKey: options.licenseKey,
    document: options.document,
    disableWebAssembly: !!options.disableWebAssembly,
    disableWebAssemblyStreaming: !!options.disableWebAssemblyStreaming,
    enableAutomaticLinkExtraction: !!options.enableAutomaticLinkExtraction,
    overrideMemoryLimit:
      typeof options.overrideMemoryLimit === 'number' ? options.overrideMemoryLimit : null,
    documentHandle: typeof options.documentHandle === 'number' ? options.documentHandle : '0',
    trustedCAsCallback:
      typeof options.trustedCAsCallback === 'function' ? options.trustedCAsCallback : null,
    customFonts: Array.isArray(options.customFonts)
      ? (options.customFonts.filter(font => font instanceof Font) as Array<Font>)
      : null,
    forceLegacySignaturesFeature: Boolean(options.forceLegacySignaturesFeature),
    electronAppName: typeof options.electronAppName === 'string' ? options.electronAppName : null,
    useSplitWASMModule: Boolean(options.useSplitWASMModule),
  }
}

export function validateStandaloneConfiguration(configuration: StandaloneConfiguration) {
  const {
    licenseKey,
    instantJSON,
    XFDF,
    disableWebAssembly,
    disableWebAssemblyStreaming,
    disableIndexedDBCaching,
    enableAutomaticLinkExtraction,
    overrideMemoryLimit,
    standaloneInstancesPoolSize,
    trustedCAsCallback,
    baseUrl,
    customFonts,
    useSplitWASMModule,
  } = configuration

  invariant(
    typeof baseUrl === 'string',
    '`baseUrl` is mandatory and must be a valid URL, e.g. `https://example.com/',
  )
  validateBaseUrl(baseUrl)
  invariant(
    licenseKey == null || typeof licenseKey === 'string',
    'licenseKey must be a string value if provided. Please obtain yours from https://customers.pspdfkit.com.',
  )

  if (typeof licenseKey === 'string') {
    invariant(
      !licenseKey.startsWith('TRIAL-'),
      `You're using the npm key instead of the license key. This key is used to download the PSPDFKit for Web package via the node package manager.

Leave out the license key to activate as a trial.`,
    )
  }

  invariant(typeof XFDF === 'undefined' || typeof XFDF === 'string', 'XFDF must be a string')

  if (instantJSON) {
    invariant(
      typeof instantJSON === 'object' && instantJSON !== null,
      'instantJSON must be an Object',
    )
    invariant(typeof XFDF === 'undefined', 'Cannot import from both instantJSON and XFDF')
    invariant(
      instantJSON.format === 'https://pspdfkit.com/instant-json/v1',
      "instantJSON has an invalid format, please use 'https://pspdfkit.com/instant-json/v1",
    )

    const { pdfId } = instantJSON

    if (pdfId) {
      invariant(
        typeof pdfId === 'object' &&
          pdfId !== null &&
          ((typeof pdfId.permanent === 'string' && typeof pdfId.changing === 'string') ||
            (typeof pdfId.permanent !== 'string' && typeof pdfId.changing !== 'string')),
        'instantJSON has an invalid pdfId',
      )
    }

    invariant(
      typeof instantJSON.skippedPdfObjectIds === 'undefined' ||
        Array.isArray(instantJSON.skippedPdfObjectIds),
      'instantJSON has invalid skippedPdfObjectIds',
    )
    invariant(
      typeof instantJSON.annotations === 'undefined' ||
        (Array.isArray(instantJSON.annotations) &&
          instantJSON.annotations.every(anno => typeof anno === 'object' && anno !== null)),
      'instantJSON has invalid annotations',
    )
    invariant(
      typeof instantJSON.formFieldValues === 'undefined' ||
        (Array.isArray(instantJSON.formFieldValues) &&
          instantJSON.formFieldValues.every(val => typeof val === 'object' && val !== null)),
      'instantJSON has invalid form field values',
    )
    invariant(
      typeof instantJSON.skippedPdfBookmarkIds === 'undefined' ||
        Array.isArray(instantJSON.skippedPdfBookmarkIds),
      'instantJSON has invalid skippedPdfBookmarkIds',
    )
    invariant(
      typeof instantJSON.bookmarks === 'undefined' ||
        (Array.isArray(instantJSON.bookmarks) &&
          instantJSON.bookmarks.every(
            bookmark => typeof bookmark === 'object' && bookmark !== null,
          )),
      'instantJSON has invalid bookmarks',
    )
  }

  invariant(
    typeof disableWebAssembly === 'undefined' || typeof disableWebAssembly === 'boolean',
    'disableWebAssembly must be a boolean',
  )
  invariant(
    typeof disableWebAssemblyStreaming === 'undefined' ||
      typeof disableWebAssemblyStreaming === 'boolean',
    'disableWebAssemblyStreaming must be a boolean',
  )
  invariant(
    typeof enableAutomaticLinkExtraction === 'undefined' ||
      typeof enableAutomaticLinkExtraction === 'boolean',
    'enableAutomaticLinkExtraction must be a boolean',
  )
  invariant(
    typeof overrideMemoryLimit === 'undefined' || typeof overrideMemoryLimit === 'number',
    'overrideMemoryLimit must be a number',
  )
  invariant(
    typeof useSplitWASMModule === 'undefined' || typeof useSplitWASMModule === 'boolean',
    'useSplitWASMModule must be a boolean',
  )
  invariant(
    typeof standaloneInstancesPoolSize === 'undefined' ||
      (typeof standaloneInstancesPoolSize === 'number' && standaloneInstancesPoolSize >= 0),
    'standaloneInstancesPoolSize must be a non-negative number',
  )
  invariant(
    typeof trustedCAsCallback === 'undefined' || typeof trustedCAsCallback === 'function',
    'trustedCAsCallback must be a function',
  )
  invariant(
    typeof customFonts === 'undefined' ||
      (Array.isArray(customFonts) && customFonts.every(font => font instanceof Font)),
    'customFonts must be an array of PSPDFKit.Font instances',
  )
  invariant(
    typeof customFonts === 'undefined' || customFonts.every(font => font.callback),
    'All PSPDFKit.Font instances specified on customFonts must have its callback property defined',
  )

  if (typeof disableIndexedDBCaching !== 'undefined') {
    deprecation(
      'disableIndexedDbCaching has been deprecated and it no longer has effect. It will be removed in a later version.\n' +
        'Browsers dropped IndexedDB serialization of Wasm modules in favor of regular HTTP caching.',
    )
  }
}

/**
 * Traverse the operations array. If an `importDocument` operation is found, which includes
 * a `document` blob, the blob will be added to the provided `operationsDocuments` object indexed
 * by the operation stringified array index and the `document` field will be set to that same value.
 *
 * Returns the processed operations array and can modify the provided `operationsDocuments` as well.
 */
async function getProcessedOperations(operations: Array<DocumentOperationJSON>) {
  // If multiple operations use the same blob, we only want to send it once.
  // We track blobs that have already been appended in a WeakMap(blob => index)
  const blobWeakMap = new WeakMap()
  const operationsDocuments = {}
  const processedOperations = await Promise.all(
    operations.map(async (operation, index): Promise<DocumentOperationJSON> => {
      if (operation.type === 'importDocument') {
        const { document } = operation

        invariant(
          document instanceof File || document instanceof Blob,
          'Wrong `importDocument` operation `document` value: it must be a File or a Blob',
        )

        return addBlobToWeakMap(
          blobWeakMap,
          operationsDocuments,
          document,
          operation,
          index,
          'document',
        )
      } else if (operation.type === 'applyInstantJson') {
        const value = operation.instantJson

        invariant(
          typeof value === 'object' && value !== null,
          'Wrong `applyInstantJson` operation `instantJson` value: it must be an object',
        )

        // convert to Blob by first converting it to JSON string
        const string = JSON.stringify(value)
        const document = new Blob([string], {
          type: 'application/json',
        })

        return addBlobToWeakMap(
          blobWeakMap,
          operationsDocuments,
          document,
          operation,
          index,
          'dataFilePath',
        )
      } else if (operation.type === 'applyXfdf') {
        const value = operation.xfdf

        invariant(
          typeof value === 'string',
          'Wrong `applyXfdf` operation `xfdf` value: it must be a string',
        )

        const document = new Blob([value], {
          type: 'application/vnd.adobe.xfdf',
        })

        return addBlobToWeakMap(
          blobWeakMap,
          operationsDocuments,
          document,
          operation,
          index,
          'dataFilePath',
        )
      }

      return operation
    }),
  )

  return {
    processedOperations,
    operationsDocuments,
  }
}
