import * as Immutable from 'immutable'
import { ID } from '@/models/annotations/Annotation'

export interface IEmbeddedFile {
  id: ID
  attachmentId: string
  description: null | string
  fileName: null | string
  fileSize: null | number
  updatedAt: null | Date
}

/**
 * @classdesc
 * This record is used to persist the information for an embedded file.
 * @public
 * @memberof PSPDFKit
 * @summary Embedded File.
 * @class EmbeddedFile
 * @noconstructor
 * @extends Immutable.Record
 * @seealso PSPDFKit.Instance#getEmbeddedFiles
 * @seealso PSPDFKit.Instance#getAttachment
 */

export class EmbeddedFile extends Immutable.Record<IEmbeddedFile>({
  id: '',

  /**
   * A unique identifier that can be used to fetch the contents of the file.
   *
   * ```js
   * const embeddedFiles = await instance.getEmbeddedFiles()
   *
   * const file = await instance.getAttachment(embeddedFiles.get(0).attachmentId)
   * ```
   *
   * @public
   * @instance
   * @member {string} attachmentId
   * @memberof PSPDFKit.EmbeddedFile
   */
  attachmentId: '',

  /**
   * The description of the file if present.
   *
   * @public
   * @instance
   * @member {?string} description
   * @memberof PSPDFKit.EmbeddedFile
   * @default null
   */
  description: null,

  /**
   * The name of the file with extension.
   *
   * @public
   * @instance
   * @member {?string} fileName
   * @memberof PSPDFKit.EmbeddedFile
   * @default null
   */
  fileName: null,

  /**
   * Size of the file in bytes.
   *
   * @public
   * @instance
   * @member {?number} fileSize
   * @memberof PSPDFKit.EmbeddedFile
   * @default null
   */
  fileSize: null,

  /**
   * The date on which the embedded file was updated.
   *
   * @public
   * @instance
   * @member {?Date} updatedAt
   * @memberof PSPDFKit.EmbeddedFile
   * @default null
   */
  updatedAt: null,
}) {}
