import { ID } from '@/models/annotations/Annotation'
import { EmbeddedFile } from '@/models/EmbeddedFile'
import { generateInstantId } from '@/models/InstantID'

interface ISerialisedEmbeddedFileServer {
  contentType: string
  description: string
  fileAttachmentId: string
  fileName: string
  fileSize: number
  type: string
  updatedAt: string
  v: number
}

interface ISerialisedEmbeddedFileStandalone {
  fileName?: string
  fileDescription?: string
  modificationDate?: string
  fileSize?: number
}

function isStandalone(
  _content: ISerialisedEmbeddedFileServer | ISerialisedEmbeddedFileStandalone,
  standalone: boolean,
): _content is ISerialisedEmbeddedFileStandalone {
  return standalone
}

export function fromJSON(
  id: ID,
  content: ISerialisedEmbeddedFileServer | ISerialisedEmbeddedFileStandalone,
  standalone = false,
): EmbeddedFile {
  if (isStandalone(content, standalone)) {
    return new EmbeddedFile({
      id: generateInstantId(),
      description: content.fileDescription,
      attachmentId: id,
      fileName: content.fileName || null,
      fileSize: content.fileSize || null,
      updatedAt: content.modificationDate ? new Date(content.modificationDate) : null,
    })
  }

  return new EmbeddedFile({
    id,
    description: content.description,
    attachmentId: content.fileAttachmentId,
    fileName: content.fileName || null,
    fileSize: content.fileSize || null,
    updatedAt: new Date(content.updatedAt) || null,
  })
}
