export function addBlobToWeakMap(
  blobWeakMap: WeakMap<File | Blob, string>,
  operationsDocuments: any,
  blob: File | Blob,
  operation: any,
  index: number,
  field: string,
) {
  let documentIndex

  // Has this blob been already appended?
  if (blobWeakMap.has(blob)) {
    // Get the index of the blob
    documentIndex = blobWeakMap.get(blob)
  } else {
    documentIndex = index.toString()
    // Add the blob to the form payload
    operationsDocuments[documentIndex] = blob
    // Track the blob
    blobWeakMap.set(blob, documentIndex)
  }

  return { ...operation, [field]: documentIndex }
}
