import { JSONToRect } from '@/lib/serializers/rect'
import { List } from 'immutable'
import { invariant } from '@pspdfkit/shared'
import SearchResult from '../models/search/SearchResult'

export function parseJsonSearchResults(json: Array<Record<string, any>>): List<SearchResult> {
  invariant(Array.isArray(json), 'Wrong `json` field')

  return List(
    json
      .map(rawResult => {
        invariant(typeof rawResult.pageIndex === 'number', 'Wrong `pageIndex` field')
        invariant(typeof rawResult.previewText === 'string', 'Wrong `previewText` field')
        invariant(Array.isArray(rawResult.rangeInPreview), 'Wrong `rangeInPreview` field')
        invariant(Array.isArray(rawResult.rectsOnPage), 'Wrong `rectsOnPage` field')

        return new SearchResult({
          pageIndex: rawResult.pageIndex,
          previewText: rawResult.previewText,
          locationInPreview: rawResult.rangeInPreview[0],
          lengthInPreview: rawResult.rangeInPreview[1],
          rectsOnPage: List(rawResult.rectsOnPage).map(jsonRect =>
            JSONToRect(jsonRect as [number, number, number, number]),
          ),
          isAnnotation: !!rawResult.isAnnotation,
          annotationRect: rawResult.annotationRect ? JSONToRect(rawResult.annotationRect) : null,
        })
      })
      .filter(Boolean),
  )
}
