import { OrderedMap, Map, set } from 'immutable'
import { Request } from './types/Request'
import { invariant } from '../../utils/invariant'

type PromiseTuple = {
  promise: Promise<any>
  resolve: (arg0: any) => unknown
  reject: (arg0: any) => unknown
}

function functionThatReturnsTrue() {
  return true
}
/**
 * The request queue is used by all backends in order to throttle concurrent
 * requests to common resources and to have a mechanism for the UI to declare
 * resources as unnecessary so that we avoid expensive computation/IO time.
 *
 * Resources can be enqueued with normal and high priority. The queue will
 * process resources that have a high priority before normal priority resources
 * (starvation, the issue that high priority work cause low priority work to
 * never run, is not handled). A maximum number of inflight requests can be
 * configured to limit concurrent requests.
 *
 * Inflight requests are not cancelled when the cancel callback is called. They
 * are just flagged as "cancelled" and their responses are ignored (we noop).
 * Cancellation only affects queued requests which are then removed from the
 * queue.
 */

export default class RequestQueue {
  queue: OrderedMap<Request, PromiseTuple>
  priorityQueue: OrderedMap<Request, PromiseTuple>
  inFlightRequests: Map<Request, PromiseTuple>
  inflightRequestLimit: number
  isDestroyed: boolean

  constructor(inflightRequestLimit: number) {
    this.queue = OrderedMap()
    this.priorityQueue = OrderedMap()
    this.inFlightRequests = Map()
    this.inflightRequestLimit = inflightRequestLimit
    this.isDestroyed = false
  }

  /**
   * Enqueues the given `request` and returns a promise, which resolves with the
   * response from the request. It's important to save the returned promise and
   * not call `.then` on it, otherwise you would get a new promise back, since
   * Promises are immutable.
   *
   * Enqueuing a request doesn't mean that your given request got resolved, but
   * you're listening now on the given response of this request.
   *
   * @example
   * const { promise, cancel } = requestQueue.enqueue(request)
   * promise.then(function (response) {
   *  // you code goes here
   * })
   */
  enqueue(
    request: Request,
    hasPriority = false,
  ): {
    promise: Promise<any>
    cancel: () => void
  } {
    if (this.isDestroyed) {
      return {
        promise: new Promise(() => {}),
        cancel: () => {},
      }
    }

    const cancel = () => {
      return this._cancel(request)
    }

    // We need the any cast here as flow does not know that the resolve and
    // reject functions are initialized synchronously.
    let resolve: any = null
    let reject: any = null
    const promise = new Promise((resolveFn, rejectFn) => {
      resolve = resolveFn
      reject = rejectFn
    })
    const targetQueue: OrderedMap<Request, PromiseTuple> = hasPriority
      ? this.priorityQueue
      : this.queue
    const newQueue = set(targetQueue, request, {
      promise,
      resolve,
      reject,
    } as PromiseTuple)

    if (hasPriority) {
      this.priorityQueue = newQueue
    } else {
      this.queue = newQueue
    }

    if (hasPriority) {
      // We can resolve the new requests immediately
      this.next()
    } else {
      // Resolve the new request asynchronously so that other requests with
      // `hasPriority: true` fires first.
      setTimeout(() => this.next(), 35)
    }

    return {
      promise,
      cancel,
    }
  }

  _cancel(request: Request): void {
    if (this.queue.has(request)) {
      this.queue = this.queue.delete(request)
    }

    if (this.priorityQueue.has(request)) {
      this.priorityQueue = this.priorityQueue.delete(request)
    }

    if (this.inFlightRequests.has(request)) {
      this.inFlightRequests = this.inFlightRequests.delete(request)
    }
  }

  /**
   * Cancels all the requests and removes all the promises
   */
  cancelAll(): void {
    this.queue = OrderedMap()
    this.priorityQueue = OrderedMap()
    this.inFlightRequests = Map()
  }

  /**
   * Resolves and then removes the queued request, identified by the given
   * `url`. Also starts the next request in the queue
   */
  _requestFinished(request: Request, response: any): void {
    if (this.isDestroyed) {
      return
    }

    const promiseTuple = this.inFlightRequests.get(request)

    if (promiseTuple) {
      promiseTuple.resolve(response)
      this.inFlightRequests = this.inFlightRequests.delete(request)
    }

    this.next()
  }

  /**
   * Rejects promise with given error and queues next request
   */
  _requestFailed(request: Request, error: Error) {
    if (this.isDestroyed) {
      return
    }

    const promiseTuple = this.inFlightRequests.get(request)

    if (promiseTuple) {
      promiseTuple.reject(error)
      this.inFlightRequests = this.inFlightRequests.delete(request)
    }

    this.next()
  }

  /**
   * Queues next request
   */
  next() {
    if (this.isDestroyed) {
      return
    }

    if (this.inFlightRequests.size >= this.inflightRequestLimit) {
      return
    }

    if (this.priorityQueue.size >= 1) {
      // Use LIFO, because we want to see the overviews that get loaded last
      // first (because often these are the tiles that came into the viewport).
      const lastEntry: void | [Request, PromiseTuple] =
        this.priorityQueue.findLastEntry(functionThatReturnsTrue)

      invariant(lastEntry)

      const [request, promiseTuple] = lastEntry

      this.priorityQueue = this.priorityQueue.delete(request)
      this.inFlightRequests = this.inFlightRequests.set(request, promiseTuple)
      request
        .request()
        .then(response => this._requestFinished(request, response))
        .catch(error => this._requestFailed(request, error))

      return
    }

    if (this.queue.size >= 1) {
      const lastEntry: void | [Request, PromiseTuple] =
        this.queue.findEntry(functionThatReturnsTrue)

      invariant(lastEntry)

      const [request, promiseTuple] = lastEntry

      this.queue = this.queue.delete(request)
      this.inFlightRequests = this.inFlightRequests.set(request, promiseTuple)
      request
        .request()
        .then(response => this._requestFinished(request, response))
        .catch(error => this._requestFailed(request, error))

      return
    }
  }

  destroy() {
    this.isDestroyed = true
  }
}
